body {
  margin: 0;
  padding: 0;
}

.stars {
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.stars__star {
  display: block;
  background-position: center;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
  height: 16px;
  width: 16px;
  margin: 0 2px;

}

.stars--0 .stars__star:nth-child(n + 6),
.stars--1 .stars__star:nth-child(n + 5),
.stars--2 .stars__star:nth-child(n + 4),
.stars--3 .stars__star:nth-child(n + 3),
.stars--4 .stars__star:nth-child(n + 2),
.stars--5 .stars__star:nth-child(n + 1) {
  background-image: url(images/star-active.svg);
}
